<script>
// 引入辅助函数
import { mapActions, mapState, mapMutations } from "vuex";
// 调用弹框
import { Dialog } from "vant";
export default {
  data() {
    return {};
  },
  created() {
    // 调用获取用户信息方法
    this.getUserInfo();
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),
    ...mapMutations(["delToken"]),
    // 退出 清空token
    logout() {
      Dialog.confirm({
        confirmButtonColor: '#7879FF',
        message: "是否退出登录？",
      })
        .then(() => {
          // on confirm
          this.delToken({ token: "" });
          this.$router.push("/login");
        })
        .catch(() => {
          // on cancel
        });
    },
  },
  computed: {
    // 获取用户信息对象
    ...mapState("user", ["userInfo"]),
  },
};
</script>

